// Start - webSocket
const reConnectWebSocket = function (url) {
  if (lockReconnect) return;
  lockReconnect = true;
  setTimeout(function () { //
    console.log('websocket');
    initSetupWebSocket(url);
    lockReconnect = false;
  }, 5000); //websocket2000ms
}

