var monitorTrack = function (monitorTrackTemp) {
  // vuex
  store.commit('emptyMonitorTrackRslt');
  // 
  ismotorTracked = true;
  monitorTrackTempRslt = [];
  var VehicleTrack_bag = WSData.CreateDataBag(WSData.DataBag.VehicleTrack);
  //Start - 
  /**
   * public enum PushMode {
        All = 0,
       Company = 1,
       Device = 2,
       MulitCompany = 3,
       MulitDevice = 4,
       Poly = 9,
       None = 100,
   }
   */
  /**
   * VehicleTrack_bag.setDeviceH(2); //Id,Id
    VehicleTrack_bag.setFeatureH(0); //0, 1
   */
  //End - 
  //Start - 
  // VehicleTrack_bag.setDeviceH(Id); //Id,Id
  // VehicleTrack_bag.setFeatureH(1); //0, 1
  //End - 

  //Start - 
  /**
   * VehicleTrack_bag.setDeviceH(21700); //Target: :0 , - ID(832)
   VehicleTrack_bag.setFeatureH(2); //Reserve:  - 44   - 2   - 2
   */
  //End - 
  //Start - 
  // VehicleTrack_bag.setDeviceH(0); //Id,Id
  // VehicleTrack_bag.setFeatureH(0); //0
  //End - 
  VehicleTrack_bag.setDeviceH(monitorTrackTemp.deviceH); //Id,Id
  VehicleTrack_bag.setFeatureH(monitorTrackTemp.featureH); //0
  send(ws, VehicleTrack_bag);
  console.log('' + new Date());
  console.log(ws);
  console.log(VehicleTrack_bag);
}
